  return (
		<div className="App">
			<Router>
				<HeaderBar/>
      	<Switch>
        	<Route exact path="/">
     	    	<Home/>
        	</Route>
        	<Route exact path="/chat">
          	<Chat/>
        	</Route>
      	</Switch>
    	</Router>
		</div>
  );
