import {useState} from 'react'
import {
 Form,
 FormControl,
 Toast,
 Button,
} from "react-bootstrap";

export default function Chat() {
	const [textInput, setTextInput] = useState({ name: '', message: '' })
  const [comments, setComments] = useState(([]))
  const handleTextInputChange = ({ target: { name, value } }) => {
  	setTextInput(prev => ({
    	...prev,
    	[name]: value,
  	}))
	}
	const handleFormSubmit = (event) => {
  	setComments(prev => [...prev, textInput])
  	setTextInput({ name: '', message: '' })
  	event.preventDefault();
	}
	return (
  	<div>
			<br/>
    	<Form inline onSubmit={handleFormSubmit}>
      	<FormControl type="input" name="name" value={textInput.name} onChange={handleTextInputChange} />
      	<FormControl type="input" name="message" value={textInput.message} onChange={handleTextInputChange} />
    		<Button variant="light" type="submit">Submit</Button>
			</Form>
			<br/>
    	<div>
      	{comments.map((comment, index) =>
					<Toast>
  					<Toast.Header>
    					<strong className="mr-auto">{comment.name}</strong>
  					</Toast.Header>
  					<Toast.Body>{comment.message}</Toast.Body>
					</Toast>
      	)}
    	</div>
  	</div>
	);

}
